<template>
  <div class="detail">
    <!-- 头部 -->
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      @click-left="goBack"
      fixed
    />
    <!-- 轮播图 -->
    <div>
      <div class="detail-img-box">
        <van-swipe indicator-color="#c43b31">
          <van-swipe-item v-for="(item, index) in ProductDetailData.pics" :key="index">
            <img class="auto-img" :src="item.pics_mid" @click="showImg" />
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <!-- 遮罩层 -->
    <van-overlay v-if="isShow" :show="show" @click="showImg">
      <div class-name="">
        <van-swipe indicator-color="#c43b31" class="mask-swipe-box">
          <van-swipe-item v-for="(item, index) in ProductDetailData.pics" :key="index">
            <img class="auto-img" :src="item.pics_mid" />
          </van-swipe-item>
        </van-swipe>
      </div>
    </van-overlay>
    <!-- 价格 -->
    <div class="detail-price-box clearfix">
      <div class="left-box fl">
        <div class="product-price fl">￥{{ ProductDetailData.goods_price }}</div>
        <div class="product-price-fake fl">￥{{ ProductDetailData.fakePrice }}</div>
        <div class="product-discount fl">{{ ProductDetailData.discount }}折</div>
      </div>
      <div class="fr reight-box">
        <img
          class="auto-img"
          src="https://h2.appsimg.com/b.appsimg.com/upload/mst/2020/12/01/133/4fcb6d13f523d17b91ca4d5d560e3ce7_207x54_90.png"
          alt=""
        />
      </div>
    </div>
    <!-- 名称 -->
    <div class="detail-name-box tow-text">
      {{ ProductDetailData.goods_name }}
    </div>
    <!-- 正品保证 -->
    <div class="detail-quality-goods clearfix">
      <div class="goods-img fl">
        <img
          class="auto-img"
          src="https://h2a.appsimg.com/b.appsimg.com/upload/momin/2019/12/23/16/1577086532213.png!85.webp"
          alt=""
        />
      </div>
      <div class="goods-title fl">100%正品 · 专业鉴定 · 正品险</div>
      <div class="goods-arrow fl">＞</div>
    </div>
    <!-- 商品参数 -->
    <div class="detail-product-parameter clearfix" @click="showEject">
      <div class="parameter-text fl">商品参数</div>
      <div class="goods-title fl">主体参数、类别参数等</div>
      <div class="parameter-arrow fl">
        <img
          class="auto-img"
          src="data:image/png;base64,"
          alt=""
        />
      </div>
    </div>
    <!-- 配送 -->
    <div class="distribution-box">
      <div class="distribution-title clearfix">
        <div class="fl distribution-text">配送至</div>

        <div @click="showAear" class="fl distribution-area">
          <div v-if="!isShowAearDta">选择地址 ＞</div>
          <div v-if="isShowAearDta">{{ selectListData }}</div>
        </div>

        <div class="fl distribution-icon" @click="showAear">
          <img src="../assets/images/area.png" class="auto-img" alt="" />
        </div>
      </div>
      <div class="distribution-bottom">现在付款，最快{{ getDate }}送达</div>
    </div>
    <!-- 地区弹出层 -->
    <van-popup v-model="isShowAear" position="bottom" :style="{ height: '50%' }">
      <van-area
        :area-list="areaList"
        :columns-num="3"
        ref="myArea"
        title="选择地区"
        @change="onChange"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
    <!-- 运费 -->
    <div class="freight-box clearfix">
      <div class="fl freight-title">运费</div>
      <div class="fl freight-text">
        新会员专享首单满38元免邮（限詠槑自营商品，部分商品不可用）
      </div>
    </div>
    <!-- 售后 -->
    <div class="detail-sale clearfix">
      <div class="sale-warpper fl">
        <div class="sale-title-box fl">
          <div class="sale-radius fl">
            <div class="sale-true">✔</div>
          </div>
          <div class="fl sale-name">詠槑发货及售后</div>
        </div>

        <div class="sale-title-box fl">
          <div class="sale-radius fl">
            <div class="sale-true">✔</div>
          </div>
          <div class="fl sale-name">顺丰配送</div>
        </div>

        <div class="sale-title-box fl">
          <div class="sale-radius fl">
            <div class="sale-true">✔</div>
          </div>
          <div class="fl sale-name">7天无理由退换</div>
        </div>

        <div class="sale-title-box fl">
          <div class="sale-radius fl">
            <div class="sale-true">✔</div>
          </div>
          <div class="fl sale-name">退货无忧</div>
        </div>

        <div class="sale-title-box fl">
          <div class="sale-radius fl">
            <div class="sale-true">✔</div>
          </div>
          <div class="fl sale-name">7天可换</div>
        </div>
      </div>

      <div class="sale-arrow fl">
        <img
          class="auto-img"
          src="data:image/png;base64,"
          alt=""
        />
      </div>
    </div>
    <!-- 商品参数弹出层 -->
    <van-popup v-model="isEject" position="bottom" round :style="{ height: '70%' }" fixed>
      <div class="eject-box">
        <div class="eject-title">商品参数</div>
        <div
          class="eject-content"
          v-for="(item, index) in ProductDetailData.attrs"
          :key="index"
        >
          <div class="eject-content-title-box clearfix">
            <div class="eject-content-title fl">{{ item.attr_name }}</div>
            <div class="eject-content-text fl tow-text">
              {{ item.attr_value }}
            </div>
          </div>
        </div>

        <!-- 底部 -->
        <div class="eject-bottom" @click="showEject">知道了</div>
      </div>
    </van-popup>
    <!-- 底部 -->
    <van-goods-action>
      <van-goods-action-icon
        icon="shopping-cart"
        text="购物车"
        :color="isAddShopcart ? '#c43b31' : '#777'"
        :badge="isAddShopcart ? count : ''"
        @click="goLive"
        id="iconBox"
      />
      <van-goods-action-icon
        icon="like"
        text="收藏"
        :color="isLike ? '#c43b31' : '#777'"
        @click="like"
      />
      <div class="custom">
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
          <div class="ball" v-show="flag" ref="ball"></div>
        </transition>
        <van-goods-action-button
          type="warning"
          @click="addShopcart"
          text="加入购物车"
          color="#777"
        />
      </div>
      <van-goods-action-button
        color="#c43b31"
        type="danger"
        text="立即购买"
        @click="buyNow"
      />
    </van-goods-action>
  </div>
</template>
<script src="https://cdn.jsdelivr.net/npm/vue/dist/vue.js"></script>
<script>
import "../assets/less/detail.less";
import areaList from "../assets/js/area";
export default {
  name: "Detail",
  data() {
    return {
      // 截取商品id
      goods_id: "",
      // 展示遮罩层
      show: true,
      // 是否显示遮罩层
      isShow: false,
      // 弹出层
      isEject: false,
      // 商品详情数据
      ProductDetailData: [],
      // 是否显示收藏高亮
      isLike: false,
      //购物车icon
      count: 0,
      //是否加入购物车
      isAddShopcart: false,
      //是否显示地址栏
      isShowAear: false,
      //地址列表
      areaList,
      // 改变时列表时数据
      carmodel: "",
      // 选中地址数据
      selectListData: "",
      //是否显示选中地区数据
      isShowAearDta: false,
      flag:false
    };
  },
  created() {
    this.goods_id = this.$route.params.goods_id;
    this.getProductDetailData();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 获取商品详情数据
    getProductDetailData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/detail",
        params: {
          goods_id: this.goods_id,
        },
      })
        .then((result) => {
          this.$toast.clear();

          this.ProductDetailData = result.data.message;
          this.ProductDetailData.fakePrice =
            result.data.message.goods_price * Math.ceil(Math.random() * 9);
          this.ProductDetailData.discount = `${Math.ceil(Math.random() * 9)}.${Math.ceil(
            Math.random() * 9
          )}`;
          if (this.ProductDetailData.goods_price == 0) {
            this.ProductDetailData.goods_price = 1888;
          }
          if (this.ProductDetailData.fakePrice == 0) {
            this.ProductDetailData.fakePrice = this.ProductDetailData.goods_price * 2;
          }
          if (this.ProductDetailData.pics.length <= 0) {
            let data = {
              pics_mid:
                "http://image5.suning.cn/uimg/b2c/newcatentries/0000000000-000000000160455569_1_800x800.jpg",
            };
            this.ProductDetailData.pics.push(data);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 显示遮罩层
    showImg() {
      if (!this.isShow) {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    },
    // 显示弹出层
    showEject() {
      this.isEject = !this.isEject;
    },
    // 加入购物车
    addShopcart() {
      //获取token
      let tokenString = localStorage.getItem("token");
      if (!tokenString) {
        this.$toast("请登陆");
        this.$router.push({ name: "Login" });
        return;
      }
      this.$toast("加入购物车成功");
      this.isAddShopcart = true;
      this.count ++;
      let myCount = this.count
      this.flag = !this.flag;
      localStorage.setItem('myCount',myCount)
    },
    // 立即购买
    buyNow() {
      if (!this.isShowAearDta) {
        return this.$toast("请选择配送地址");
      }
      this.$router.push({ name: "Live" });
      this.$toast("购买成功");
    },
    // 收藏
    like() {
      //获取token
      let tokenString = localStorage.getItem("token");
      if (!tokenString) {
        this.$toast("请登陆");
        this.$router.push({ name: "Login" });
        return;
      }
      if (!this.isLike) {
        this.$toast("收藏成功");
        this.isLike = true;
      } else {
        this.$toast("取消收藏");
        this.isLike = false;
      }
    },
    // 跳转购物车页面
    goLive() {
      if (!this.isAddShopcart) {
        return this.$toast("购物车里什么也木有...");
      }
      this.$router.push({
        name: "Live",
        params: { goods_id: this.ProductDetailData.goods_id },
      });
    },
    // 展示地区
    showAear() {
      this.isShowAear = true;
    },
    // 选择地区
    onChange(picker, index, value) {
      //value=0改变省，1改变市，2改变区
      let val = picker.getValues();
      let areaName = "";
      for (var i = 0; i < val.length; i++) {
        areaName = areaName + (i == 0 ? "" : "/") + val[i].name;
      }
      this.carmodel = areaName;
    },
    //确定选择城市
    onConfirm(val) {
      this.selectListData = val[0].name + "," + val[1].name + "," + val[2].name;
      this.isShowAear = false;
      this.isShowAearDta = true;
    },
    //取消选中城市
    onCancel() {
      // 重置城市列表
      this.$refs.myArea.reset();
    },
    // 小球抛物线
    beforeEnter(el,x=0,y=0) {
      el.style.transform = `translate(${x},${y})`;
      // 初始位置
    },
    enter(el, done) {
      el.offsetWidth; //用于刷新动画，没有的话没效果
      //h5新api--getBoundingClientRect，可以获取自身一个矩形的坐标（上下左右）
      //es6 模板字符串
      el.style.transform = `translate(${-90}px, ${-20}px)`;
      //贝塞尔曲线
      el.style.transition = "all .5s";
      done(); //必须加，执行的是after里边的代码
    },
    afterEnter(el) {
      this.flag = !this.flag; //状态切换，为下一次动画做准备
    },
  },
  computed: {
    // 计算月日
    getDate() {
      let date = new Date();
      return `${date.getMonth() + 1}月${date.getDate() + 3}日`;
    },
  },
};
</script>

<style lang="less" scoped></style>
